.main {
  grid-template-columns: repeat(2, 200px);
  gap: 30px 40px;
  padding: 30px 35px;
  display: grid;
}

.main__item {
  width: 200px;
  height: auto;
  position: relative;
}

.main__item:hover {
  background-color: #eee;
  border-radius: 10px;
  width: 220px;
  height: 287px;
}

.main__item.image-container {
  width: 200px;
}

.main__image {
  border-radius: 200px;
  width: 200px;
  height: 200px;
  transition: transform 1s;
  display: inline-block;
}

.main__image:hover {
  border-radius: 60px;
  transform: scale(1.2);
}

.main__rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50px;
  width: 32px;
  height: 32px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 32px;
  display: block;
  position: absolute;
  top: 84px;
  left: 158px;
  right: 10px;
}

.details {
  text-align: center;
  color: #2f4f4f;
  margin-top: 8px;
  font-family: Arial;
  font-size: 20px;
  line-height: 23px;
  position: relative;
}

.extra {
  background-color: #fff;
  width: 100%;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
}

.main__item:hover .extra {
  display: block;
}

@media (width >= 750px) {
  .main {
    grid-template-columns: repeat(2, 200px);
    padding: 30px 160px;
  }
}

@media (width >= 1000px) {
  .main {
    grid-template-columns: repeat(4, 200px);
    padding: 30px 55px;
  }
}

@media (width >= 1500px) {
  .main {
    grid-template-columns: repeat(4, 200px);
    padding: 30px 305px;
  }
}
